<template>
  <div id="nav" class="nav">

    <!-- 导航栏左侧 -->
    <div class="nav-left">
      <a class="nav-link" target="_blank" href="//github.com/gengwenhao">
        <i style="margin-right: 4px;"
           class="fa fa-github-alt"
           aria-hidden="true"></i>
        GitHub
      </a>
      <a class="nav-link" target="_blank" href="https://www.cnblogs.com/gengwenhao">个人博客</a>
      <!-- 搜索框 -->
      <div class="input-group">
        <i @click="onSearch" class="fa fa-search" aria-hidden="true"></i>
        <input v-model="searchText" @keyup.enter="onSearch" type="text" placeholder="search">
      </div>
    </div>

    <!-- 导航栏右侧 -->
    <div class="nav-right">

      <!-- 已登录 -->
      <div v-if="isLogin"
           class="user-info animated fadeIn">
        <a @mouseenter="onUserInfoHover"
           class="nav-link">
          用户信息
          <i style="margin-left: 4px;"
             class="fa fa-info-circle"
             aria-hidden="true">
          </i>
        </a>
        <!-- 注销按钮 -->
        <a class="nav-link red" @click="onLogoutBtnClick">
          注销&nbsp;{{username}}
          <i style="margin-left: 4px;" class="fa fa-sign-out" aria-hidden="true"></i>
        </a>
      </div>
      <!-- 用户详情 -->
      <div @mouseleave="isShowUserInfo=false" v-if="isShowUserInfo" class="userinfo-detail">
        <div class="ui header dividing">
          用户收藏数&nbsp;{{favCount}}<i style="margin-left: 4px;" class="fa fa-spin fa-refresh" aria-hidden="true"></i>
        </div>
        <span>最近收藏：</span>
        <span @click="downFav" class="ui button inverted red pull-right">下载全部收藏<i style="margin-left: 4px;"
                                                                                  class="fa fa-download"
                                                                                  aria-hidden="true"></i></span>
        <ul>
          <li v-for="favItem in favList" v-bind:key="favItem.id">
            <a target="_blank" :href="favItem.url">{{favItem.title}}</a>
          </li>
        </ul>
      </div>

      <!-- 未登录 -->
      <div v-if="!isLogin" class="user-info">
        <a @click="onLoginBtnClick" class="nav-link">
          登录用户
          <i style="margin-left: 4px;"
             class="fa fa-sign-in"
             aria-hidden="true">
          </i>
        </a>
        <login-panel class="animated fadeIn" @onRegBtnClick="onRegBtnClick"
                     @onLoginPanelBGClick="onLoginPanelBGClick"
                     v-if="isShowLoginPanel">
        </login-panel>
        <reg-panel class="animated fadeIn" @onRegPanelBGClick="onRegPanelBGClick"
                   v-if="isShowRegPanel">
        </reg-panel>
      </div>
    </div>
  </div>
</template>

<script>
  import LoginPanel from "./LoginPanel"
  import {checkLogin, getFavList, logout, searchNews} from "../api/api"
  import RegPanel from "./RegPanel"
  import FileSaver from "file-saver"

  export default {
    name: "Nav",
    components: {RegPanel, LoginPanel},
    computed: {
      isLogin() {
        return this.$store.state.isLogin
      },
      favCount() {
        return this.$store.state.favList.length
      },
      favList() {
        return this.$store.state.favList.slice(0, 15)
      }
    },
    data() {
      return {
        searchText: '',
        username: '',
        password: '',
        isShowLoginPanel: false,
        isShowRegPanel: false,
        isShowUserInfo: false
      }
    },
    methods: {
      onUserInfoHover() {
        this.isShowUserInfo = true
        getFavList()
          .then((resp) => {
            this.$store.commit('updateFavList', resp.data)
          })
      },
      downFav() {
        var content = JSON.stringify(this.$store.state.favList);
        var blob = new Blob([content], {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(blob, "favlist.json");
      },
      onRegBtnClick() {
        this.isShowLoginPanel = false
        this.isShowRegPanel = true
      },
      onLogoutBtnClick() {
        logout()
          .then((data) => {
            if (data.data[0].status === 1) {
              this.$store.commit('logout')
            }
          })
      },
      onSearch() {
        searchNews(this.searchText)
          .then((data) => {
            if (!data.result) {
              alert('未找到相关新闻')
            } else {
              location.href = `/?search=${this.searchText}`
            }
          })
      },
      onLoginBtnClick() {
        this.isShowRegPanel = false
        this.isShowLoginPanel = true
      },
      onLoginPanelBGClick() {
        this.isShowLoginPanel = false
        this.username = ''
        this.password = ''
      },
      onRegPanelBGClick() {
        this.isShowRegPanel = false
        this.username = ''
        this.password = ''
      }
    },
    created() {
      checkLogin()
        .then((data) => {
          this.username = data.data[0].user.username
          this.$store.commit('login')
        })
        .catch(() => {
          this.$store.commit('logout')
        })

      getFavList()
        .then((resp) => {
          this.$store.commit('updateFavList', resp.data)
        })
    }
  }
</script>

<style scoped lang="scss">
  #nav {
    padding: 0 32px;
    height: 34px;
    line-height: 34px;
    background: #222;
    font-size: 14px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;

    .nav-left {
      display: flex;
      align-items: center;

      .input-group {
        display: flex;
        align-items: center;
        padding-left: 4px;
        margin-left: 12px;
        background: rgba(white, .9);
        line-height: 12px;
        height: 24px;

        i {
          cursor: pointer;
        }

        input {
          background: transparent;
          border: none;
          text-indent: 2px;
          color: #b2b2b2;
          font-size: 12px;
        }
      }

    }

    .nav-right {
      // 用户信息
      .user-info {
        a:nth-child(1) {
          margin-right: 12px;
        }

        a:nth-child(2) {
          padding: 4px;
        }
      }

      .userinfo-detail {
        padding: 15px;
        top: 34px;
        right: 0;
        cursor: pointer;
        position: absolute;
        float: right;
        width: 500px;
        background-color: #fff;

        span {
          color: #333333;
          font-weight: bold;
        }

        span.down {
          float: right;
        }

      }
    }
  }
</style>
