<template>
  <div id="mask" class="mask" @click.stop="onRegPanelBGClick($event)">
    <notifications></notifications>
    <div id="login-panel">
      <div class="ui middle aligned center aligned grid">
        <div class="column">
          <h2 class="ui inverted grey image header">
            <div class="content">
              注册新账号
            </div>
          </h2>
          <form class="ui large form">
            <div class="ui stacked segment">
              <div class="field">
                <div class="ui left icon input">
                  <i class="user icon"></i>
                  <input ref="inputusername" @blur="onCheckUsername" v-model="username" type="text" placeholder="用户名">
                </div>
              </div>
              <div class="field">
                <div class="ui left icon input">
                  <i class="lock icon"></i>
                  <input ref="inputpassword" v-model="password"
                         type="password"
                         name="password"
                         placeholder="密码">
                </div>
              </div>
              <div class="field">
                <div class="ui left icon input">
                  <i class="lock icon"></i>
                  <input @keyup.enter="onRegBtnClick"
                         v-model="passwordRepeat"
                         type="password"
                         name="password"
                         placeholder="密码确认">
                </div>
              </div>
              <div id="login-button" @click="onRegBtnClick" class="ui fluid large teal button">注册</div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {checkUserName, register} from "../api/api"

  export default {
    name: "RegPanel",
    data() {
      return {
        username: '',
        password: '',
        passwordRepeat: ''
      }
    },
    methods: {
      onCheckUsername() {
        checkUserName(this.username)
          .then((resp) => {
            if (resp.data.status) {
              this.$refs.inputusername.focus()
              this.$notify({message: resp.data.result})
            }
          })
      },
      onRegPanelBGClick(event) {
        if (event.target.classList.contains('mask')) {
          this.$emit('onRegPanelBGClick')
        }
      },
      onRegBtnClick() {
        if (this.passwordRepeat !== this.password) {
          this.$notify({message: '两次输入的密码不一样哦!'})
          this.$refs.inputpassword.focus()
          this.passwordRepeat = ''
        } else if (this.passwordRepeat === "" || this.password === "") {
          this.$notify({message: '输入不允许为空!'})
        } else {
          register(this.username, this.passwordRepeat)
            .then((resp) => {
              if (resp.status) {
                this.$notify({message: 'resp.result'})
                this.$emit('onRegPanelBGClick')
              }
            })
        }
      }
    }
  }
</script>

<style scoped lang="scss" ref="stylesheet/scss">
  #mask {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: fixed;
    background-color: rgba(black, .9);
    z-index: 1;

    #login-panel {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

      #login-button {
        background: #ca0c16;

        &:hover {
          background: #b60b14;
        }
      }
    }
  }
</style>
