<template>
  <div id="news" class="center w">
    <notifications></notifications>

    <div class="c-left">
      <img @click="goBackHome" @mousedown.prevent="" class="logo-img" src="../assets/logo.png" alt="">
      <div class="text">
        <a @click="updateDetail(index)" :class="{'active':activeIndex==index}"
           v-for="(item,index) in channel"
           v-bind:key="item.id"
           href="#">{{item}}</a>
      </div>
    </div>

    <div class="c-center">
      <div class="detail">
        <h1 v-if="isSearchResult">搜索结果..</h1>
        <div v-for="(item,index) in detail" v-bind:key="item.id" class="item">
          <div class="import-all">
            <img :src="item.pic" alt="">
            <div class="i-one-text">
              <a @click.stop="showDetail(index)" href="#">{{item.title}}</a>
              <div class="i-one-logo">
                <a href="#">{{item.src}} ⋅{{item.time}}</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="c-right" v-if="isShowDetail">
      <div class="toolbar">
        <span @click="addFavURLBtnClick" class="add_favurl">
          添加收藏
          <i style="margin-left: 2px;"
             class="fa fa-heart-o"
             aria-hidden="true"></i></span>
      </div>
      <i @click="isShowDetail=false" class="fa fa-times detial-close" aria-hidden="true"></i>
      <div class="detail animated fadeIn news-detail ui piled segment" v-html="detailHTML" v-if="isShowDetail">
      </div>
    </div>
  </div>
</template>

<script>
  import {addFavURL, queryChannel, queryDetail, searchNews} from "../api/api"

  export default {
    name: "News",
    methods: {
      updateDetail(index) {
        // 更新当前激活频道
        this.activeIndex = index

        // 获取新闻详情
        queryDetail(this.channel[index])
          .then(data => {
            this.detail = data.result.list
          })
      },
      showDetail(index) {
        this.detailTitle = this.detail[index].title.trim()
        this.detailURL = this.detail[index].url.trim()
        this.detailHTML = this.detail[index].content
        this.isShowDetail = true
      },
      addFavURLBtnClick() {
        addFavURL(this.detailTitle, this.detailURL)
          .then((resp) => {
            this.$notify({message: resp.data.result})
          })
      },
      goBackHome() {
        location.href = '/'
      }
    },
    data() {
      return {
        channel: [],
        detail: {},
        activeIndex: 0,
        isShowDetail: false,
        detailHTML: '',
        detailURL: '',
        detailTitle: '',
        isSearchResult: false
      }
    },
    created() {
      let params = location.search.replace('?', '').split('&')
      let kv = {}
      for (let i = 0; i < params.length; i++) {
        let splArr = params[i].split('=')
        if (splArr && splArr.length > 1) {
          kv[splArr[0]] = splArr[1]
        }
      }

      if ('search' in kv) {
        this.searchText = decodeURI(kv['search'])
        searchNews(this.searchText)
          .then((data) => {
            this.detail = data.result.list
            this.channel = []
          })
        this.isSearchResult = true
      } else {
        queryChannel()
          .then(data => {
            this.channel = data.result
            this.updateDetail(0)
          })
      }
    }
  }
</script>

<style lang="scss" scoped="scoped">
  #news {
    .c-left {

      .logo-img {
        position: relative;
        right: 150px;
      }
    }

    .c-right {
      margin-top: 25px;
      padding: 50px;
      width: 80%;
      height: 80%;
      z-index: 1;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      overflow: scroll;
      background-color: rgba(255, 255, 255, 1);

      .toolbar {
        width: 80%;
        border-bottom: 1px solid #efefef;
        margin-bottom: 23px;
        padding-bottom: 4px;
        font-size: 15px;
        color: #222;
      }

      .detial-close {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 50px;
        color: #b2b2b2;
      }
    }


  }
</style>
