import axios from 'axios'

const HOST = 'https://api.jisuapi.com'
// const APP_KEY = '7e030e5b91997b0c'
const APP_KEY = '7850b0e5d08266f1'

//axios本版本不支持jsonp 自己拓展一个
axios.jsonp = (url) => {
  if (!url) {
    window.console.error('Axios.JSONP 至少需要一个url参数!')
    return;
  }
  return new Promise((resolve) => {
    window.jsonCallBack = (result) => {
      resolve(result)
    }
    var JSONP = document.createElement("script");
    JSONP.type = "text/javascript";
    JSONP.src = `${url}&callback=jsonCallBack`;
    document.getElementsByTagName("head")[0].appendChild(JSONP);
    setTimeout(() => {
      document.getElementsByTagName("head")[0].removeChild(JSONP)
    }, 500)
  })
}

// 查询频道
export const queryChannel = () => {
  return axios.jsonp(`${HOST}/news/channel?appkey=${APP_KEY}`)
}

// 查询详情
export const queryDetail = (tag) => {
  return axios.jsonp(`${HOST}/news/get?channel=${tag}&start=0&num=10&appkey=${APP_KEY}`)
}

// 搜索新闻
export const searchNews = (keyword) => {
  return axios.jsonp(`${HOST}/news/search?keyword=${keyword}&appkey=${APP_KEY}`)
}

// 登录
export const login = (username, password) => {
  let data = {
    username,
    password
  }
  return axios.post(`/login.do`, data)
}

// 注册
export const register = (username, password) => {
  let data = {
    username,
    password
  }
  return axios.post(`/register.do`, data)
}

// 注销
export const logout = () => {
  return axios.get(`/logout.do`)
}

// 检查登录状态
export const checkLogin = () => {
  return axios.get(`/check_login.do`)
}

// 添加收藏
export const addFavURL = (title, url) => {
  let params = {
    title,
    url
  }
  return axios.get(`/add_favurl.do`, {params})
}

// 获取收藏列表
export const getFavList = () => {
  return axios.get(`/get_fav.do`)
}

// 检查用户名是否存在
export const checkUserName = (username) => {
  return axios.get(`/check_username.do`, {params: {username}})
}
