<template>
  <div id="mask" class="mask" @click.stop="onLoginPanelBGClick($event)">
    <notifications></notifications>
    <div id="login-panel">
      <div class="ui middle aligned center aligned grid">
        <div class="column">
          <h2 class="ui inverted grey image header">
            <div class="content">
              登录到账号
            </div>
          </h2>
          <form class="ui large form">
            <div class="ui stacked segment">
              <div class="field">
                <div class="ui left icon input">
                  <i class="user icon"></i>
                  <input v-model="username" type="text" placeholder="用户名">
                </div>
              </div>
              <div class="field">
                <div class="ui left icon input">
                  <i class="lock icon"></i>
                  <input @keyup.enter="onLoginBtnClick"
                         v-model="password"
                         type="password"
                         name="password"
                         placeholder="密码">
                </div>
              </div>
              <div id="login-button" @click="onLoginBtnClick" class="ui fluid large teal button">登录</div>
            </div>
          </form>

          <div class="ui message">
            新用户？ <a @click.prevent="onRegBtnClick">注册</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {login, logout} from "../api/api"

  export default {
    name: "LoginPanel",
    data() {
      return {username: '', password: ''}
    },
    methods: {
      onLoginBtnClick() {
        if (this.username !== '' && this.password !== '') {
          login(this.username, this.password)
            .then((data) => {
              if (data.data[0].status === 1) {
                this.$store.commit('login')
                this.isShowLoginPanel = false
              } else {
                this.username = ''
                this.password = ''
                this.$notify({message: '登录失败, 请检查用户名或密码!'})
              }
            })
            .catch(() => {
              this.username = ''
              this.password = ''
              this.$notify({message: '登录失败, 请检查用户名或密码!'})
            })
        } else {
          this.$notify({message: '请检查输入'})
        }
      },
      onRegBtnClick() {
        this.$emit('onRegBtnClick')
      },
      onLoginPanelBGClick(event) {
        if (event.target.classList.contains('mask')) {
          this.$emit('onLoginPanelBGClick')
        }
      }
    }
  }
</script>

<style scoped lang="scss" ref="stylesheet/scss">
  #mask {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: fixed;
    background-color: rgba(black, .9);
    z-index: 1;

    #login-panel {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

      #login-button {
        background: #ca0c16;

        &:hover {
          background: #b60b14;
        }
      }
    }
  }
</style>
