import Vue from 'vue'
import Vuex from 'vuex'
import Notify from 'vue-notifyjs'
import App from './App.vue'
import 'vue-notifyjs/themes/now-ui.scss'

Vue.config.productionTip = false

Vue.use(Vuex)
Vue.use(Notify, {type: 'primary', timeout: 2000})

const store = new Vuex.Store({
  state: {
    isLogin: false,
    favList: []
  },
  mutations: {
    login(state) {
      state.isLogin = true
    },
    logout(state) {
      state.isLogin = false
    },
    updateFavList(state, favList) {
      state.favList = favList
    }
  }
})

new Vue({
  render: h => h(App),
  store
}).$mount('#app')
